<template>
<el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
    prop="id"
    label="ID"
    width="50">
    </el-table-column>
    <el-table-column
    prop="name"
    label="Name"
    width="180"><el-link href="https://element.eleme.io" target="_blank">default</el-link>
    </el-table-column>
    <el-table-column
    prop="link"
    label="Link"
    width="400">
    </el-table-column>
</el-table>
</template>

<script>
import axios from 'axios'
import {BACKEND_URL} from '../../util/const'

export default {
    name: 'LDA_Table',
    props: {
        num: Number
    },
    methods: {
        getData() {
            this.topic_data = this.getDataTopic()
        },
        getDataTopic () {
            const path = BACKEND_URL + '/name' 
            axios.get(path)
                .then(response => {
                this.topic_data = response.data
                this.filldata()
                })
                .catch(error => {
                console.log(error)
            })
        },
        filldata () {
            this.tableData = this.topic_data['data'][this.num]['data']
        }
    },
    data() {
    return {
        topic_data: null,
        tableData: null,
    }
    },
    created () {
        this.getData()
  }
}
</script>